<template>
<v-theme-provider theme="dark" with-background class="pa-10">
    <v-container class="mt-3 mb-1 welcome" >
        <h1 class="white--text display-2" align="center">Dashboard</h1>
        <div>
            <v-container>
                <div class="my-3 mx-auto">
                    <v-row>
                        <v-col xs="12" md="3" align-self="center">
                            <v-spacer></v-spacer>
                            <v-btn  bottom
                                    class="mx-5"
                                    color="green lighten-5"
                                    dark
                                    fab
                                    fixed
                                    right
                                    @click='openPopUpAddContent'
                            >
                                <v-icon>mdi-plus</v-icon>Create New Content
                            </v-btn><v-spacer></v-spacer>
                        </v-col>
                        <v-col xs="12" md="3" class="ml-2" align-self="center">
                            <v-text-field required clearable label="Filter Title" v-model="newFilterTag" @keydown.enter="addFilterTag"></v-text-field>
                        </v-col>
                        <!--<v-col xs="12" md="3" class="ml-2" align-self="center">
                            <v-btn @click="addFilterTag" style="width: 100%">
                                Add Filter Tag
                            </v-btn>
                        </v-col>-->

                        <v-col xs="6" md="2" class="ml-2" align-self="center">
                          <v-btn @click = "applyFilter">
                            Apply Filter
                            </v-btn>
                        </v-col>
                        <v-col xs="6" md="2" class="ml-2" align-self="center">
                            <v-btn @click="clearFilter" style="width: 100%">
                                Clear Filter
                            </v-btn>
                        </v-col>
                    </v-row>
                </div>
                <div class="px-3">
                    <v-row>
                        <v-col xs="12" sm="6" md="4" lg="3" xl="2" v-for="cnt in content" :key="cnt.title">
                            <v-card class="ma-2" tile>
                                <v-card-title style="overflow:hidden; text-overflow: ellipsis; white-space: nowrap">
                                        {{cnt.title}}
                                    </v-card-title>
                                <v-img  height="200" width="100%" class="white--text align-start" cover :src="`${cnt.imageUrl}`">

                                </v-img>
                                <v-card-actions>
                                    <v-chip-group column>
                                        <v-chip v-for="tag in cnt.tags" :key="tag">
                                            {{tag}}
                                        </v-chip>
                                    </v-chip-group>
                                </v-card-actions>
                            </v-card>
                        </v-col>
                    </v-row>
                </div>
                
            </v-container>
        </div>
        
    </v-container>
    <v-dialog v-model="dialogAddContent" width="500"  @click:outside="closePopUpAddContent">
        <v-progress-circular v-if="uploadingImage"
            :size="50"
            color="green"
            indeterminate
        ></v-progress-circular>
        <v-card v-if="!uploadingImage">
            <v-card-title class="headline grey lighten-2" style="word-break: normal">
                <h3>Create some content</h3>
            </v-card-title>
            <v-card-text>
                <v-row no-gutters>
                    <v-col cols="1"><v-icon>mdi-pen</v-icon></v-col>
                    <v-col class="ml-2">
                        <v-text-field required clearable label="Add a title" v-model="newTitle"></v-text-field>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="1"><v-icon>mdi-image</v-icon></v-col>
                    <v-col class="ml-2">
                        <v-file-input accept="image/*" label="upload image" prepend-icon="" @change="fileSelected"></v-file-input>
                        <v-img v-if="newImageFile" :src="newImageFile"></v-img>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="1"><v-icon>mdi-tag</v-icon></v-col>
                    <v-col cols="7" class="ml-2">
                        <v-text-field required clearable label="Add a tag" v-model="newTag"></v-text-field>
                    </v-col>
                    <v-col class="ml-2" align-self="center">
                        <v-btn color="secondary" @click="addTag" style="width: 100%">
                            Add Tag
                        </v-btn>
                    </v-col>
                </v-row>
                <v-row>
                    <v-chip-group column>
                        <v-chip v-for="tag in newTags" :key="tag">
                            {{tag}}
                        </v-chip>
                    </v-chip-group>
                </v-row>
            </v-card-text>
            <v-card-actions>
                <v-row>
                    <v-col cols="6">
                        <v-btn outline color="green" @click="submit" style="width: 100%">
                            Submit
                        </v-btn>
                    </v-col>
                    <v-col><v-btn outline @click="closePopUpAddContent" color="red" style="width: 100%">Cancel</v-btn><v-spacer/>
                    </v-col>
                </v-row>
            </v-card-actions>
        </v-card>
    </v-dialog>
  </v-theme-provider>
    
    
</template>

<style scoped>
</style>

<script>
import { defineComponent } from 'vue'

export default defineComponent({

  name: 'HelloWorld',

  data() {
      return {
          axios: null,
          //backendURL: "http://35.196.202.145:5000",
          backendURL: "",
          content: null,

          filteredContent: [],

          dialogAddContent: false,
          selectedFile: null,

          newFilterTag: '',
          filterTags: [],
          
          newTitle: null,
          newBase64Image: null,
          newImageFile: null,
          newTags: [],
          newTag: null,

          uploadingImage: false,
          
      }
  },

  mounted() {
           
      this.axios = require("axios").default;
      var hostname = window.location.hostname;
      console.log(hostname)
      this.backendURL = "http://" + hostname + ":5000";
      
      /*this.axios.get('http://api.ipify.org:80/')
        .then(
          response => {
              console.log(response.data);
              this.backendURL = "http://" + response.data + ":5000";
              this.axios.get(this.backendURL + '/allCards')
                .then(response => (this.content = response.data))
                .catch(e => (console.log(e)));
          }
      );*/
      
      //this.getBackendUrl().then(response => (this.backendURL = response));
      this.axios.get(this.backendURL + '/allCards')
        .then(response => (this.content = response.data))
        .catch(e => (console.log(e)));
  },

  methods: {
      openPopUpAddContent() {
          this.dialogAddContent = true;
          console.log(this.dialogAddContent)
        },

      closePopUpAddContent() {
          this.newTitle = null;
          this.newImageUrl = null;
          this.newTags = [];
          this.dialogAddContent = false;
        },
    fileSelected(evt) {
        evt.preventDefault()
        console.log(evt);
        //this.newImageUrl = URL.createObjectURL(evt.target.files[0])
        this.newImageFile = evt.target.files[0]
        //this.createBase64Image(evt.target.files[0]);
    },

    createBase64Image(fileObject) {
        const reader = new FileReader();

        reader.onload = (e) => {
            this.newBase64Image = e.target.result;
        };
        reader.readAsBinaryString(fileObject);
    },

    addFilterTag() {
        if(!this.filterTags.includes(this.newFilterTag)) {
            this.filterTags.push(this.newFilterTag);
        }
    },

    removeFilterTag(tag) {
        let idx = this.filterTags.indexOf(tag);
        this.filterTags = this.filterTags.splice(idx, 1);
    },

    applyFilter() {
        
        let args = "tags=";
        if(this.newFilterTag === '') {
            this.axios.get(this.backendURL + '/allCards' ).then(response => (this.content = response.data));
            //this.axios.get(this.backendURL + '/cards' ).then(response => (console.log(response)));
            return;
        }
        /*for(let j = 0; j < this.filterTags.length; j++) {
            args = args + this.filterTags[j] + ",";
        }*/
        args = args + this.newFilterTag;
        this.axios.get(this.backendURL + '/cardsByTags?' + args).then(response => (this.content = response.data));
        //this.axios.get(this.backendURL + '/cardsByTags?' + args).then(response => (console.log(response)));

        /*
        this.filteredContent = [];
        for (let i = 0; i < this.content.length; i++) {
            console.log(this.filterTags[i]);
            for(let j = 0; j < this.filterTags.length; j++) {
                if(this.content[i].tags.includes(this.filterTags[j])) {
                    this.filteredContent.push(this.content[i]);
                }
            }
        }*/
    },

    clearFilter() {
        this.newFilterTag = ''
        this.axios.get(this.backendURL + '/allCards')
        .then(response => (this.content = response.data))
        .catch(e => (console.log(e)));
    },
    
    addTag() {
        this.newTags.push(this.newTag);
    },

    async submit() {
        var formData = new FormData();
        formData.append("image", this.newImageFile);
        this.uploadingImage = true;
        const imgUrl = await this.axios.post(this.backendURL + '/upload_image', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
        console.log(imgUrl);
        const resp = await this.axios.post(this.backendURL + '/card', {
            title: this.newTitle,
            imageUrl: imgUrl.data,
            tags: this.newTags
        });
        this.uploadingImage = false;
        //console.log(resp);
        /*
        this.content = [];
        for (let i = 0; i < resp.data.length; i++) {
            const newEntry = {
                'title': resp.data.title,
                'imageUrl': resp.data.imageUrl,
                'tags': resp.data.tags
            }
            this.content.append(newEntry)
        }*/
        this.content = resp.data
        //this.axios.get(this.backendURL + '/allCards').then(response => (this.content = response.data));
        //this.axios.get(this.backendURL + '/cards').then(response => (console.log(response)));        
        //this.content.push({title: this.newTitle, img: this.newImageUrl, tags: this.newTags});
        this.closePopUpAddContent();
    },

    getAllCards() {
        let cntn = null;
         this.axios.get(this.backendURL + '/cards').then(function (response) {
            //this.content = re
            //this.content = response.data
            cntn = response.data;
            console.log(cntn.length);
            this.content = cntn;
            console.log(this.content);
        });
        /*
        console.log("Iam here");
        console.log(cntn.length)
        for (let i = 0; i < cntn.length; i++) {
            console.log(cntn[i].title);
        }
        return cntn;*/
    }
}
    /*
    computed: {
        url() {
            if (!this.image) return;
            console.log(URL.createObjectURL(this.image));
            return URL.createObjectURL(this.image);
          
        }
    }*/
})
</script>